import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { IssueReceiveGoodsComponent } from './issue-receive-goods.component';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { AddComponent } from './add/add.component'; 
import { DetailComponent } from './detail/detail.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { DetailComponentShopee } from './detailShopee/detail.component';
import { EditComponent } from './edit/edit.component';
import { BillType3ShopeeModule } from 'src/app/sales/components/bill-type/bill-type-3/shopee/bill-type1-shopee.module';

const routes: Routes = [
  {
    path: "",
    component: IssueReceiveGoodsComponent
  },
  {
    path: "add",
    component: AddComponent
  },
  {
    path: "edit",
    component: EditComponent
  },
  {
    path: "detail",
    component: DetailComponent
  },
  {
    path: "detail-tiktok",
    component: DetailComponentShopee
  },
]

@NgModule({
  declarations: [
    IssueReceiveGoodsComponent, AddComponent, DetailComponent, DetailComponentShopee, EditComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule,
    BillType3ShopeeModule
  ]
})
export class IssueReceiveGoodsModule { }
