import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhEcommerce, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { differenceInCalendarDays } from 'date-fns';
@Component({
  selector: 'app-issue-receive-goods',
  templateUrl: './issue-receive-goods.component.html',
  styleUrls: ['./issue-receive-goods.component.scss']
})
export class IssueReceiveGoodsComponent implements OnInit {
  searchValue: any;
  salesOrder: Array<any> = []
  salesSubOrder: Array<any> = []
  heightScroll: any;
  public date: Date[] = [new Date(), new Date()];
  load_status: boolean = false;
  dataRestore: any = {};
  bill_selected: any = {}
  list_status_bill: Array<any> = [
    { name: "All", value: "all" },
    { name: "Wait for confirmation", value: "UNPAID", value2: 'TO_CONFIRM_RECEIVE' },
    { name: "Chờ lấy hàng", value: "READY_TO_SHIP" },
    { name: "Shipping", value: "PROCESSED" },
    { name: "Delivered", value: "SHIPPED" },
    { name: "Hoàn tất", value: "COMPLETED" },
    { name: "Cancellation", value: "CANCELLED" },
  ];
  /**Giới hạn thời gian cho xem bill */
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < this.minDate.getTime() - 24 * 60 * 60 * 1000) return true
    return false
  }
  minDate: Date = new Date(Date.now() - 24 * 60 * 60 * 1000 * 30)
  infoShop: any = {}
  constructor(
    private router: Router,
    public lang: LanguageService,
    public vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private cdRef: ChangeDetectorRef,
    private datePipe: DatePipe,
    private vhEcommerce: VhEcommerce,
    private vhAuth: VhAuth
  ) {
    this.date[0].setHours(0, 0, 0, 0)
    this.date[1].setHours(23, 59, 59, 59)
    this.dataRestore = this.router.getCurrentNavigation().extras.state?.dataRestore || {}
  }
  ngOnInit(): void {
    this.initState()
  }
  initState() {
    if (this.dataRestore.id_shop_selected) {
      this.date = this.dataRestore.date
      this.searchValue = this.dataRestore.searchValue
      this.salesOrder = this.dataRestore.salesOrder
      this.salesSubOrder = this.salesOrder
      if (this.dataRestore['update']) {
        this.dataRestore['update'] = false;
        this.showReport()
      }
      else
        this.load_status = true;
    }
    this.infoShop = this.vhEcommerce.getlocalShopListTiktok()[0];
    this.getMinDate()
  }
  /**
   * Lấy ngày giới hạn được xem báo cáo
  */
  getMinDate() {
    let date = new Date(Date.now() - 24 * 60 * 60 * 1000 * 100)
    this.vhQuerySales.getValidityDate(date).then((validitydate: Date) => {
      this.minDate = validitydate
      if ((this.minDate.getTime()) < new Date(this.vhAuth.getStore().registrationdate).getTime()) {
        this.disabledDate = (current: Date): boolean => {
          if (current.getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime() - 24 * 60 * 60 * 1000) return true
          return false
        }
      }
    })
  }
  /**Tìm kiếm hóa đơn */
  search(value: string) {
    let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
    this.salesOrder = this.vhAlgorithm.searchList(val, [...this.salesSubOrder], ['order_sn'])
  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/issue-invoice'], {
      state: { dataRestore: this.dataRestore }
    })
  }
  changeTime(event) {
    if (event[1].getTime() - event[0].getTime() <= 1209600000) {
    } else {
      this.vhComponent.showToast(2000, this.lang.translate("Thời gian xem không quá 15 ngày"), "alert-toast")
      this.date = [event[0], new Date(event[0].getTime() + 1209600000)]
    }
  }
  showReport() {
    this.load_status = true;
    this.salesOrder = []
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.vhEcommerce.getSalesOrderListTiktok(new Date(this.datePipe.transform(new Date(this.date[0].setHours(0, 0, 0)), "yyyy-MM-dd HH:mm:ss").toString()), new Date(this.datePipe.transform(new Date(this.date[1].setHours(23, 59, 59)), "yyyy-MM-dd HH:mm:ss").toString()), this.infoShop.shop_cipher, this.infoShop.app_key, this.infoShop.access_token.toString(), this.infoShop._id.toString())
        .then((result: any) => {
          this.vhComponent.hideLoading(0);
          result.forEach(item => {
            if (item.verify_status_received_goods)
              this.salesOrder.push({ ...item, create_at: new Date(item.create_time * 1000) })
          })
          this.salesOrder = this.processOrders(this.salesOrder);
          this.salesSubOrder = [...this.salesOrder];
        })
        .catch(err => this.vhComponent.hideLoading(0))
    })
  }

  processOrders(orders) {
    // .filter(e => e.cqt_status != 3)
    return orders.map(order => {
      const lineItemMap = {};

      order.line_items.forEach(item => {
        const key = `${item.product_id}_${item.sku_id}_${item.sale_price}`;
        item.price = Number(item.sale_price);

        if (!lineItemMap[key]) {
          lineItemMap[key] = {
            ...item,
            quantity: 1,
            total_price: item.price,
          };
        } else {
          lineItemMap[key].quantity += 1;
          lineItemMap[key].total_price += item.price;
        }

      });
      order.create_at = new Date(order.create_time * 1000);
      return {
        ...order,
        order_details: Object.values(lineItemMap),
      };
    });
  }

  changeShop(event) { }
  ngAfterViewChecked() {
    if (document.querySelector("#tiktok-issue-invoice") && document.querySelector(".ant-table-thead") && document.querySelector(".tiktok-issue-invoice-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#tiktok-issue-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".tiktok-issue-invoice-header").clientHeight - 90 + "px";
    }

    this.cdRef.detectChanges();
  }
  gotoCreateBill(bill: any) {
    //tìm customer để loại trừ bug khách hàng bị xóa rồi
    let customer: any = this.vhQuerySales.getlocalCustomer(this.infoShop.data?.id_customer)
    if (customer) {
      this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/issue-invoice/issue-receive-goods/add'], {
        state: { dataRestore: { ...this.dataRestore, searchValue: this.searchValue, id_shop_selected: this.infoShop._id, date: this.date, salesOrder: this.salesOrder }, billTiktok: bill }
      })
    } else this.vhComponent.alertMessageDesktop('warning', this.lang.translate('Please update customer in settings'))
  }
  gotoDetail(bill: any) {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/issue-invoice/issue-receive-goods/detail'], {
      state: { dataRestore: { ...this.dataRestore, searchValue: this.searchValue, id_shop_selected: this.infoShop._id, date: this.date, salesOrder: this.salesOrder }, id: bill.id_bill_received_goods, billShopee: bill }
    })
  }
  gotoDetailTiktok(bill) {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/issue-invoice/issue-receive-goods/detail-tiktok'],
      {
        state: {
          bill: bill,
          dataRestore: {
            salesOrder: this.salesOrder, searchValue: this.searchValue,
            id_shop_selected: this.infoShop._id, listProduct: this.dataRestore.listProduct,
            date: this.date,
            salesSubOrder: this.salesSubOrder,
          }
        }
      })
  }
}
